
.header[data-v-8c99e32e]{
	background-color: whitesmoke;
	position:fixed;
	top:0px;
	width: 100%;
	z-index: 99;
	height: 60px;
	line-height: 60px;
	text-align: center;
	max-height: 100px;
	overflow:hidden;
}
.navToMain[data-v-8c99e32e]{
  position: absolute;
  top:18px;
  left:18px;
  height: 24px;
  width:24px;
  /*background-color: red;*/
  z-index: 120;
  background-image: url(/static/imgs/back.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.navToMain a[data-v-8c99e32e]{
  display: inline-block;
  height: 24px;
  width:24px;
}



