
.footer[data-v-cefeab12]{
	background-color: whitesmoke;
	position:fixed;
	bottom:0px;
	width: 100%;
	height: 85px;
	overflow:hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border-top: 1px lightgrey solid;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.main[data-v-cefeab12],.articles[data-v-cefeab12],.home[data-v-cefeab12]{
	height: 70px;
	width: 100px;
	/*background-color: red;*/
	text-align: center;
	line-height: 65px;
}
.footer a[data-v-cefeab12]{
	display: block;
	height: 100%;
	width: 100%;
}
.main a[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/main.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
.articles a[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/articles.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
.home a[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/home.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
/*active*/
.main a.router-link-exact-active[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/main-s.png');
}
.articles a.router-link-exact-active[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/articles-s.png');
}
.home a.router-link-exact-active[data-v-cefeab12]{
	background-image: url('../../src/assets/toolbar/home-s.png');
}
.main[data-v-cefeab12],.articles[data-v-cefeab12],.home[data-v-cefeab12]{
	position: relative;
	/*border:solid 1px red;*/
}
.footer span[data-v-cefeab12]{
	position: absolute;
	display: block;
	height: 24px;
	font-size: 10px;
	bottom: 18px;
	left: 50%;
	width:24px;
	z-index: -1;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}



